import React from 'react';
import img1 from '../../images/images-day/1.jpg';
import img2 from '../../images/images-day/2.jpg';
import img3 from '../../images/images-day/3.jpg';
import img4 from '../../images/images-day/4.jpg';
import img5 from '../../images/images-day/5.jpg';
import img6 from '../../images/images-day/6.jpg';
import img7 from '../../images/images-day/7.jpg';
import cn from 'classnames';
import style from './ToDo.module.css';
const ToDoImagesBlock = ({currentDay}) => {
    return (
        <div>
            {currentDay === 'ПН' && <div className={cn(style.img)}><img src={img1}/></div>}
            {currentDay === 'ВТ' && <div className={cn(style.img)}><img src={img2}/></div>}
            {currentDay === 'СР' && <div className={cn(style.img)}><img src={img3}/></div>}
            {currentDay === 'ЧТ' && <div className={cn(style.img)}><img src={img4} /></div>}
            {currentDay === 'ПТ' && <div className={cn(style.img)}><img src={img5} /></div>}
            {currentDay === 'СБ' && <div className={cn(style.img)}><img src={img6} /></div>}
            {currentDay === 'ВС' && <div className={cn(style.img)}><img src={img7} /></div>}
            
        </div>
    )
}
export default ToDoImagesBlock;