const SET_CURRENT_DAY = 'SET_CURRENT_DAY';
const SET_CHECKED_METHOD = 'SET_CHECKED_METHOD';
const NEW_CREATE_TODO = 'NEW_CREATE_TODO';
const SET_OUTSTANDING_TASKS = 'SET_OUTSTANDING_TASKS';
let initialState = {
    toDo : [],
    currentDay: ''
}
export const toDoReducer = (state=initialState,action) => {
    switch (action.type) {
        case SET_CURRENT_DAY: {
            return {
                ...state,
                currentDay: action.currentDay
            }
        }
        case SET_CHECKED_METHOD: {
            return {
                ...state,
                toDo: [...state.toDo.map(t => {
                    if(t.id === action.id) {
                        return {...t,checked:action.value,isFetching: action.editIsFetching}
                    }
                    return t
                })]
            }
        }
        case NEW_CREATE_TODO: {
            const newToDo = {
                id: state.toDo.length + 1,
                title: action.title,
                completed: false,
                checked: null,
                isFetching: false

            }
            return {
                ...state,
                toDo: [...state.toDo,newToDo]
            }
        }
        case SET_OUTSTANDING_TASKS: {
            return {
                ...state,
                toDo: state.toDo.filter(t=>t.checked === 'bookMark' || t.checked === null)
            }
        }
    }
    return state
}
export const setCurrentDay  = (currentDay) => ({type:SET_CURRENT_DAY,currentDay})
export const setOutstandingTasks  = () => ({type:SET_OUTSTANDING_TASKS})
export const newCreateToDo  = (title) => ({type:NEW_CREATE_TODO,title})
export const setCheckedMethod  = (value,id,editIsFetching) => ({type:SET_CHECKED_METHOD,value,id,editIsFetching})