import React from 'react';
import cn from 'classnames';
import style from './ToDoItem.module.css';
import checkedImg from './../../../images/tick.png';
const ToDoItem = ({toDo,setCheckedMethod}) => {
    const handleSetCheckedMethod = (value,id,isFetching) => {
        if(isFetching === true) {
            const editIsFetching = isFetching = false;
            setCheckedMethod(null,id,editIsFetching);
        } else {
            const editIsFetching = isFetching = true;
            setCheckedMethod(value,id,editIsFetching);
        }
    } 
    return (
        <div className={cn(style.wrapper)}>
            {toDo.map(t => {
                return <div className={cn(style.toDoItem)} key={t.id}>
                    <div>{t.title}</div>
                    <div className={cn(style.markBlock)}>
                        <div className={cn(style.checkMark)}>
                            <button 
                            data-value='tick' 
                            onClick={(e) => handleSetCheckedMethod(e.currentTarget.dataset.value,t.id,t.isFetching)} 
                            className={style.btnChecked}>
                                <img src={checkedImg} className={t.checked === "tick" ?style.selectTick:style.tick}/>
                            </button>
                        </div>
                        <div className={cn(style.cross)}>
                            <button 
                            data-value='bookMark' 
                            onClick={(e) => handleSetCheckedMethod(e.currentTarget.dataset.value,t.id,t.isFetching)} 
                            className={style.btnChecked}>
                                <div className={t.checked === "bookMark" ? style.markSelect : style.d30}></div>
                            </button>
                        </div>
                    </div>
                </div>
            })}
        </div>
    )
}
export default ToDoItem;