import React, { useEffect, useState }  from "react";
import cn from "classnames";
import style from "./ToDo.module.css";
import ToDoItem from "./ToDoItem/ToDoItem";
import { connect } from "react-redux";
import { getToDo, getCurrentDay } from "../../redux/toDo-selects";
import { setCurrentDay,newCreateToDo,setCheckedMethod,setOutstandingTasks} from '../../redux/toDo-reducer';
import ToDoImagesBlock from "./ToDoImagesBlock";
import { getCurrentDayHelper } from './../../Helper/currentDay';
const ToDo = ({toDo,setCurrentDay,currentDay,setCheckedMethod,currentData,newCreateToDo,setOutstandingTasks}) => {
  const [finishIsFetching,setFinishIsFetching] = useState(null);
  const [value,setValue] = useState('');
  const data = currentData + '';
  useEffect(() => {
      const day = getCurrentDayHelper();
      setCurrentDay(day);
  },[]) 
  const handleOnChange = (e) => {
    setValue(e.currentTarget.value);
  }
  const createNewTask = (e) => {
    if(e.key === 'Enter') {
      if(value != '') {
        newCreateToDo(value);
        setValue('');
      }
    }
  }
  const finishDay = () => {
    let isFetchingStatus = null;
    const isFetchingArr = [];
    toDo.forEach(element => {
      isFetchingArr.push(element.checked)
    });
    if(isFetchingArr.length === 0 || isFetchingArr === null) {
      return isFetchingStatus = false;
    }
    setOutstandingTasks();
    return isFetchingStatus = isFetchingArr.every(i=>i==='tick');
  }
  
  return (
    <div className={cn(style.toDo)}>
        <div className={cn(style.title)}>Список дел {data}</div>
        <div></div>
        <div className={cn(style.imgBlock)}>
            <ToDoImagesBlock currentDay={currentDay} />
            {finishIsFetching === 'successes' && <div className={style.finalText}>Выполнено!</div>}
        </div>
        <div className={cn(style.toDoItem)}>
          <ToDoItem toDo={toDo} setCheckedMethod={setCheckedMethod}/>
        </div>
        <div className={style.block}>
          <div className={style.textareaWrapper}>
            <input placeholder="Введите текст" value={value} className={style.textarea} onChange={handleOnChange} onKeyPress={createNewTask}/>
          </div>
          <div className={style.btnWrapper}><button className={style.btn} onClick={() => {
            const finishIsFetching = finishDay();
            if(finishIsFetching === true) {
              setFinishIsFetching('successes');
              setTimeout(() => {
                setFinishIsFetching(null)
              }, 3000);
            }
          }}>Завершить день</button></div>
        </div>
    </div>
  );
};
const mapStateToProps = (state) => ({
  toDo: getToDo(state),
  currentDay: getCurrentDay(state),
  currentData: state.app.currentData
})
export default connect(mapStateToProps,{setCurrentDay,setCheckedMethod,newCreateToDo,setOutstandingTasks})(ToDo);
