const SET_INITIALIZED = 'SET_INITIALIZED';
const SET_CURRENT_DATA = 'SET_CURRENT_DATA';
let initialState = {
    initialized: false,
    currentData: null
}
export const appReducer = (state=initialState,action) => {
    switch (action.type) {
        case SET_INITIALIZED: {
            return {
                ...state,
                initialized: true
            }
        }
        case SET_CURRENT_DATA: {
            return {
                ...state,
                currentData: action.data
            }
        }
    }
    return state
}
export const setInitialized = () => ({type:SET_INITIALIZED});
export const setCurrentData = (data) => ({type:SET_CURRENT_DATA,data});
export const initializedApp = () => (dispatch) => {
    const dateObg = new Date;
    const year = dateObg.getFullYear();
    const month = dateObg.getMonth()+1+'';
    const date = dateObg.getDate()+'';
    dispatch(setCurrentData(`${date.length === 1 ? '0' + date : date}.${month.length === 1 ? '0' + month : month}.${year}`));
    dispatch(setInitialized());
}
